.maps-test {
  sort-map-map: '(
    alpha: (
      min-width: 20em
    ),
    beta: (
      min-width: 30em,
      test: hello
    ),
    gamma: (
      min-width: 48em
    ),
    delta: (
      min-width: 64em,
      test: world
    ),
    epsilon: (
      min-width: 80em
    )
  )';
  sort-map-list: '(min-width: 20em) (min-width: 30em) (min-width: 48em, test: 'hello') (min-width: 64em) (min-width: 80em, test: 'world')';
  collect-map-map: '(
    beta: (
      min-width: 30em,
      test: hello
    ),
    delta: (
      min-width: 64em,
      test: world
    )
  )';
  collect-map-list: '(min-width: 48em, test: 'hello') (min-width: 80em, test: 'world')'; }

